<template>
  <div class="hello">
     <video id="video" width="375" height="320" controls></video>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },

  mounted(){
    if(navigator.mediaDevices&&navigator.mediaDevices.getUserMedia){
        navigator.mediaDevices.getUserMedia({
            'audio':true,
            'video':{ 'facingMode': "user" }//调用前置摄像头，后置摄像头使用video: { facingMode: { exact: "environment" } }
        })
        .then((mediaStream)=>{
            this.getVideoStream(mediaStream)
        })
        .catch((err) => {
            alert(err)
        })
video.srcObject    }
  },
  methods:{
    getVideoStream(mediaStream){
        var video = document.getElementById("video");  
        video.srcObject = mediaStream;  
        myVvideoideo.onloadedmetadata = function () {
          // video.controls = "controls";
          video.play();
        }
    }
  }
}
</script>

